import notList from "@/components/notList.vue";
import ArticleList from "./articleList-model.js";
const articleList = new ArticleList();
export default {
	components: {
	  notList
	},
	data() {
		return {
			userInfo: {},
			articleList: {
				page: 1,
				data: []
			}
		}
	},
	onLoad(options) {
		const that = this
		that.options = options
		that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			that.userInfo = that.$store.state.userInfo;
			this.getArticleList(() => {
				callBack && callBack();
			})
		},
		// 加载资讯列表
		getArticleList(callBack) {
			const that = this
			articleList.getArticleList({
				page: that.articleList.page || 1
			}, (res) => {
				if (res.code == 4000) {
					let list = res.data
					for (let i = 0; i < list.length; i++) {
						list[i].createtime = articleList.transformTime(list[i].createtime * 1000)
					}
					res.data = that.articleList.data.concat(list); 
					that.articleList = res
				}
				callBack && callBack();
			})
		},
		// 进入--资讯--详情页
		goInformationDetails(e) {
			const that = this;
			const id = articleList.get_data_set(e, "id");
			articleList.navigate_to(`/pages/informationDetails/informationDetails?id=${id}`);
		},
	},
	// 下拉刷新
	onPullDownRefresh() {
		var that = this;
		that.articleList = {
			page: 1,
			data: []
		}
		that._onLoad(() => {
			uni.stopPullDownRefresh();
		});
	},
	//上拉加载更多
	onReachBottom() {
	  var that = this;
	  if (that.articleList.page == that.articleList.page_number) {
			articleList.show_tips('没有更多数据了')
	    return;
	  }
	  that.articleList.page += 1;
	  // 客户经理列表加载
	  this.getArticleList()
	},
	// 分享
	onShareAppMessage() {
		// let shareData = {
		// 	title: '',
		// 	path: `pages/index/index`,
		// 	imageUrl: ''
		// }
		return articleList.onShareAppMessage({});
	}
}
